<template>
  <div id="app">
    <times class="time" />
  </div>
</template>
<script>
import { mapState } from "vuex";
import { mapMutations } from "vuex";
import { SET_TIME } from "./store/type";

import Times from "./components/time/Time.vue";

import initTime from "./utils/initTime.js";
export default {
  components: { Times },
  data() {
    return {
      timer: "",
    };
  },
  computed: {
    ...mapState(["type"]),
  },
  methods: {
    ...mapMutations([SET_TIME]),
    getTime() {
      this.timer = setInterval(() => {
        this.SET_TIME(initTime());
      }, 1000);
    },
  },
  created() {
    this.getTime();
  },
  beforeDestroy() {
    clearInterval(this.timer);
  },
};
</script>
<style lang="scss">
html,
body,
#app {
  font-size: 16px;
  width: 100%;
  height: 100%;
}
#app {
  background-color: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
  &.dark {
    background-color: rgb(26, 25, 25);
  }
}
</style>
