<template>
  <div class="timenumber">
    <span>{{ timeNumbers }}</span>
  </div>
</template>
<script>
import { mapState } from "vuex";
export default {
  props: ["timetype"],
  computed: {
    ...mapState(["time"]),
    timeNumbers() {
      const times =
        this.timetype === "hour"
          ? this.time.hour
          : this.timetype === "minutes"
          ? this.time.minutes
          : this.time.seconds;

      return times > 9 ? times : `0${times}`;
    },
  },
};
</script>
<style lang="scss">
.timenumber {
  font: {
    size: 25vh;
  }
  color: #cb8a14;
  text-shadow: 0 0 16px;
}
</style>
