<template>
  <div class="point" :class="{ hide: flag }">
    <span></span>
    <span></span>
  </div>
</template>
<script>
import { mapState } from "vuex";
export default {
  data() {
    return {
      flag: false,
    };
  },
  computed: {
    ...mapState(["time"]),
  },
  watch: {
    time: {
      handler() {
        this.flag = !this.flag;
      },
      deep: true,
    },
  },
};
</script>
<style lang="scss">
.point {
  height: 8vh;
  width: 3vh;
  display: inline-flex;
  flex-direction: column;
  justify-content: space-between;
  &.hide {
    visibility: hidden;
  }
  span {
    display: inline-block;
    width: 3vh;
    height: 3vh;
    border-radius: 50%;
    background-color: #ecad36;
  }
}
</style>
