import Vue from "vue";
import Vuex from "vuex";
import { SET_TIME } from "./type";
Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    type: "clock",
    time: {},
  },
  getters: {},
  mutations: {
    [SET_TIME](state, payload) {
      state.time = payload;
    },
  },
  actions: {},
  modules: {},
});
